.modal {
    display: none;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 99;
    background-color: rgba(0, 0, 0, 0.6);
  }
  .modal button {
    outline: none;
    cursor: pointer;
    border: 0;
  }
  .modal > section {
    width: 90%;
    max-width: 48vw;
    margin: 0 auto;
    border-radius: 0.3rem;
    background-color: #fff;
    /* 팝업이 열릴때 스르륵 열리는 효과 */
    animation: modal-show 0.3s;
    overflow: hidden;
    font-family: 'pretendard';
    font-size: 1.5vw;
    font-weight: bold;
  }
  .modal > section > main {
    padding: 1.2vw;
    height: 6.5vw;
    padding-top: 2.5vw;
    text-align: center;
    border-top: 1px solid #dee2e6;
  }
  .modal > section > footer {
    padding: 1.1vw 1.3vw;
    text-align: right;
  }
  .modal > section > footer button {
    padding: 0.5vw 1vw;
    color: black;
    background-color: #ffdb86;
    border-radius: 5px;
    font-size: 1.1vw;
    font-family: 'pretendard';
    font-weight: bold;
  }
  .modal.openModal {
    display: flex;
    align-items: center;
    /* 팝업이 열릴때 스르륵 열리는 효과 */
    animation: modal-bg-show 0.3s;
  }
  @keyframes modal-show {
    from {
      opacity: 0;
      margin-top: -50px;
    }
    to {
      opacity: 1;
      margin-top: 0;
    }
  }
  @keyframes modal-bg-show {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }